.filters-list .li {
    font-family: 'Abril Fatface', cursive;
    font-weight: bolder;
    font-size: 18px;
    position: relative;
    /* display: inline-block; */
    padding: 5px 35px;
    margin: 25px 0;
    color: #87C232 !important;
    background-color: #0005;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.5s;
    letter-spacing: 4px;
    overflow: hidden;
}

.filters-list .li:hover,
.filters-list .li.active {
    background: #87C232;
    color: #050801 !important;
    box-shadow: 0 0 5px #87C232, 0 0 25px #87C232, 0 0 50px #87C232, 0 0 200px #87C232;
    -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
}